<template>
    <div class="share">
        <van-popup
        v-model="shareShow"
        position="bottom"
        :close-on-click-overlay="false"
        @click-overlay="clickOverlay"
        >
        <ul class="list flex justify-space-between">
            <li @click="toshare('wx')">
                <img src="../../assets/images/pic_weixin.png" >
                <p>微信</p>
            </li>
            <li @click="toshare('pyq')">
                <img src="../../assets/images/pic_pengyouquan.png" >
                <p>朋友圈</p>
            </li>
            <li @click="toshare('qq')">
                <img src="../../assets/images/pic_qq.png" >
                <p>QQ</p>
            </li>
            <li @click="toshare('wb')">
                <img src="../../assets/images/pic_weibo.png" >
                <p>微博</p>
            </li>
        </ul>
        <div class="cancel" @click="handleCacel">
            取消
        </div>
        </van-popup>
    </div>
</template>

<script>
export default {
    props:['shareShow'],
    data(){
        return {
           
        }
    },
    created(){

    },
    methods:{
        toshare(v){
            let url = `${this.$store.state.indexUrl+'my/index/square?'+this.$store.state.appParams}`,
                json = {url:url,channel:'wxp,wxf,sina,qqp,qqz',title:'title',desc:'desc',image:'https://cdn.e-power.vip/share_logo.png'};
            window.location.href= `teldapp://share?params=`+JSON.stringify(json)
            // if(!this.$store.state.userData){
           
            //     window.location.href= "teldapp://login"
            // }else {
            //     window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)
       
            // }
            switch(v){
                case 'wx':
                    json.channel ='wxp'
                    break
                case 'pyq':
                    json.channel ='wxf'
                    break
                case 'qq':
                    json.channel ='qqp'
                    break;
                case 'wb':
                    json.channel ='sina'
                    break            
            }
            this.$emit('handleShareEvent',false)
            console.log(`teldapp://share?params=`+JSON.stringify(json));
            window.location.href= `teldapp://share?params=`+JSON.stringify(json)
        },
        clickOverlay(){
           this.$emit('handleShareEvent',false) 
        },
        handleCacel(){
            this.$emit('handleShareEvent',false)
        },
    }
}
</script>

